@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

@font-face{
    font-family: "Roboto";
    src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap")
}

*{
    margin:0;
    box-sizing:border-box;
    font-family: "Roboto";
    padding:0;
    }
    
.rec-arrow{
    display: none;
}

    .search__main{
        box-shadow: -5px -5px 15px #edeff1, 10px 10px 15px #cacfd8;
        height: 40px;
        width: 1000px;
        border-radius: 50px;
        padding: 0px 10px;
        background-color: #FAFAFC;
    }
    
    
    .search__input{
        text-align: start;
        padding-top: 5px;
        background-color: transparent;
        border: none;
        outline: none;
        border-radius: 30px;
        font-size: 19px;
        padding-left: 25px;
        color:#4A506D;
        width: 800px;
    }
    
    .search__label{
        padding-left: 10px;
    }
    
    .header__main{
        padding-top: 10px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    .btn__header{
        border-radius: 50px;
        padding: 10px 50px;
        box-shadow: -5px -5px 15px #edeff1, 10px 10px 15px #cacfd8;
        border: none;
        font-weight: bold;
        background-color: #FAFAFC;
        font-family: 'Roboto', sans-serif;
        color: #4A506D;
        margin-left: 10px;
    }
    
    .btn__settings{
        background-color: #FAFAFC;
        border-radius: 50px;
        padding: 10px 10px;
        box-shadow: -5px -5px 15px #edeff1, 5px 5px 10px #cacfd8;
        border: none;
        font-family: 'Roboto', sans-serif;
        color: gray;
        margin-left: 10px;
    }
    
    .card__main :hover{
        box-shadow: -5px -5px 15px #edeff1, 10px 10px 15px #cacfd8;
    }

    .card__box :hover{
        box-shadow: none;
    }

    .menu__text{
        color: #4A506D;
        margin-left: 20px;
        margin-top: 30px;
    }
    
    .card__main{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    
    button.rec-dot{
        display: none;
      }
      

    .card__box{
        font-size: 25px;
        color: #4A506D;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color:  #F7F7F7;
        border-radius: 10px;
        margin-left: 2px;
        margin-right: 2px;
        margin-top: 16px;
        width: 280px;
        height: 200px;
    }
    
    .promotions__box{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    
    }
    
    .slider{
        padding: 0px 10px 0px 10px;
    }
    
    .blog-card {
        display: flex;
        flex-direction: column;
        margin: 1rem auto;
        box-shadow: 0 3px 7px -1px rgba(0, 0, 0, .1);
        margin-bottom: 1.6%;
        background: #fff;
        margin-left: 10px;
        line-height: 1.4;
        font-family: sans-serif;
        border-radius: 10px;
        overflow: hidden;
        z-index: 0;
    }
    .blog-card a {
        color: inherit;
    }
    .blog-card a:hover {
        color:  #4A506D;
    }
    .blog-card:hover .photo {
        transform: scale(1.3) rotate(3deg);
    }
    .blog-card .meta {
        position: relative;
        z-index: 0;
        height: 200px;
    }
    .blog-card .photo {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-size: cover;
        background-position: center;
        transition: transform 0.2s;
    }
    .blog-card .details, .blog-card .details ul {
        margin: auto;
        padding: 0;
        list-style: none;
    }
    .blog-card .details {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -100%;
        margin: auto;
        transition: left 0.2s;
        background: rgba(0, 0, 0, .6);
        color: #fff;
        padding: 10px;
        width: 100%;
        font-size: 0.9rem;
    }
    .blog-card .details a {
        text-decoration: dotted underline;
    }
    .blog-card .details ul li {
        display: inline-block;
    }
    .blog-card .details .author:before {
        font-family: FontAwesome;
        margin-right: 10px;
        content: "\f007";
    }
    .blog-card .details .date:before {
        font-family: FontAwesome;
        margin-right: 10px;
        content: "\f133";
    }
    .blog-card .details .tags ul:before {
        font-family: FontAwesome;
        content: "\f02b";
        margin-right: 10px;
    }
    .blog-card .details .tags li {
        margin-right: 2px;
    }
    .blog-card .details .tags li:first-child {
        margin-left: -4px;
    }
    .blog-card .description {
        padding: 1rem;
        background: #fff;
        position: relative;
        z-index: 1;
    }
    .blog-card .description h1, .blog-card .description h2 {
        font-family: Poppins, sans-serif;
    }
    .blog-card .description h1 {
        line-height: 1;
        margin: 0;
        font-size: 1.7rem;
    }
    .blog-card .description h2 {
        font-size: 1rem;
        font-weight: 300;
        text-transform: uppercase;
        color: #a2a2a2;
        margin-top: 5px;
    }
    .blog-card .description .read-more {
        text-align: right;
    }
    .blog-card .description .read-more a {
        color:  #4A506D;
        display: inline-block;
        position: relative;
    }
    .blog-card .description .read-more a:after {
        content: "\f061";
        font-family: FontAwesome;
        margin-left: -10px;
        opacity: 0;
        vertical-align: middle;
        transition: margin 0.3s, opacity 0.3s;
    }
    .blog-card .description .read-more a:hover:after {
        margin-left: 5px;
        opacity: 1;
    }
    .blog-card p {
        position: relative;
        margin: 1rem 0 0;
    }
    .blog-card p:first-of-type {
        margin-top: 1.25rem;
    }
    .blog-card p:first-of-type:before {
        content: "";
        position: absolute;
        height: 5px;
        background:  #4A506D;
        width: 35px;
        top: -0.75rem;
        border-radius: 3px;
    }
    .blog-card:hover .details {
        left: 0%;
    }

    .btn__adminSettings{
        margin-left: 10px;
        color: #4A506D;
        border-radius: 50px;
        border: none;
        cursor: pointer;
    }

    .menu__main{
        display: flex;
        align-items: center;
    }

    .btn__menu{
        margin-top: 35px;
        margin-left: 10px;
        border-radius: 50px;
        border: none;
    }

    .btn__adminSettings :hover{
        box-shadow: 0 3px 7px -1px rgba(0, 0, 0, .1);
    }

    .multi-button{
        top: 1.25rem;
        left: 1.25rem;
        border-radius: 100%;
        width: 0rem;
        height: 0rem;
        z-index: 0;
    }

    .btns__settings{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 2rem;
        height: 2rem;
        border: none;
        border-radius: 100px;
        color: brown;
        box-shadow: 0 0 0rem, -0.25rem blue;
    }

